                  <mu-flex class="flex-demo">
                    <mu-icon value="volume_up"></mu-icon>
                  </mu-flex>
                  <mu-flex class="flex-demo" fill>
                    <mu-slider class="demo-slider" v-model="volume" color="white"></mu-slider>
                  </mu-flex>
                </mu-flex> -->
