    getUrlKey(name) {
      if (window.location.href.indexOf("?houseId") != -1) {
        let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        let r = decodeURIComponent(window.location.search).substr(1).match(reg);  //url"?"
        let context = "";
        if (r != null)
          context = r[2];
        reg = null;
        r = null;
        return context == null || context == "" || context == "undefined" ? "" : context;
      } else {
        return null;
      }
    },
